import React, { useState } from 'react';
import { StaticQuery } from 'gatsby'

import Box from 'components/Box';
import Flex from 'components/Flex';
import Circle from 'components/Circle';
import Fb from 'components/Fb';
import Line from 'components/Line';
import Button from 'components/Button';
import Image from 'components/Image';
import Text from 'components/Text';
import Modal from 'components/Modal';

import share from './share.svg';
import withResponsive from '../../hoc/withResponsive'

const Sharer = ({ isMobile }) => {
  const [open , setOpen] = useState(0)
  return (
    <StaticQuery
      query={graphql`
      query SiteUrlQuery {
        site {
          siteMetadata {
            url
          }
        }
      }`}
      render={({ site: { siteMetadata: { url } } }) => (
        <div>
          <Box textAlign="center">
              <Button my="2em" onClick={() => setOpen(1)}>分享看診好幫手給親友</Button>
            </Box>
            <Modal
              isOpen={Boolean(open)}
              onRequestClose={() => setOpen(0)}
              customStyles={{ width: isMobile ? null : '25%' }}
            >
              <Box textAlign="center">
                <Image width="70%" src={share} />
                <Text color="titleBlue" fontSize="1.6em" mt="2em" fontWeight="bold">分享給親友</Text>
                <Text mt="0.5em" fontSize="1.2em">一起當看診小達人！</Text>
                <Flex justifyContent="center" mt="2em">
                  <Box width="5em" mx="0.25em">
                    <Circle is={Button.download} onClick={() => window.open(`http://www.facebook.com/share.php?u=${url}`)}><Fb width="5em" /></Circle>
                  </Box>
                  <Box width="5em" mx="0.25em">
                    <Circle is={Button.download} onClick={() => window.open(`https://social-plugins.line.me/lineit/share?url=${url}`)}><Line width="5em" /></Circle>
                  </Box>
                </Flex>
              </Box>
            </Modal>
        </div>
      )}
    />
  );
};

export default withResponsive(Sharer);
